// src/app/api/facebook-ads/route.ts
import { NextRequest, NextResponse } from "next/server";
/**
 * Facebook Marketing API Route
 *
 * à¸”à¸¶à¸‡à¸‚à¹‰à¸­à¸¡à¸¹à¸¥à¹à¸„à¸¡à¹€à¸›à¸à¸ˆà¸²à¸ Facebook Ads
 */
interface FacebookAdsCampaign {
  id: string;
  name: string;
  status: string;
  objective: string;
  impressions: number;
  clicks: number;
  spend: number;
  cpm: number;
  cpc: number;
  ctr: number;
  conversions: number;
  costPerConversion: number;
}
interface FacebookAdsResponse {
  campaigns: FacebookAdsCampaign[];
  summary: {
    totalImpressions: number;
    totalClicks: number;
    totalSpend: number;
    averageCpm: number;
    averageCpc: number;
    averageCtr: number;
    totalConversions: number;
  };
  dateRange: {
    startDate: string;
    endDate: string;
  };
}
export async function GET(request: NextRequest) {
  try {
    const searchParams = request.nextUrl.searchParams;
    const startDate = searchParams.get("startDate") || "2025-01-01";
    const endDate = searchParams.get("endDate") || "2025-04-04";
    // à¸•à¸£à¸§à¸ˆà¸ªà¸­à¸š credentials
    const credentials = {
      appId: process.env.FACEBOOK_APP_ID,
      appSecret: process.env.FACEBOOK_APP_SECRET,
      accessToken: process.env.FACEBOOK_ACCESS_TOKEN,
      adAccountId: process.env.FACEBOOK_AD_ACCOUNT_ID,
    };
    const missingCredentials = [];
    if (!credentials.appId) missingCredentials.push("FACEBOOK_APP_ID");
    if (!credentials.appSecret) missingCredentials.push("FACEBOOK_APP_SECRET");
    if (!credentials.accessToken)
      missingCredentials.push("FACEBOOK_ACCESS_TOKEN");
    if (!credentials.adAccountId)
      missingCredentials.push("FACEBOOK_AD_ACCOUNT_ID");
    if (missingCredentials.length > 0) {
      console.error("âŒ Missing Facebook Ads credentials:", missingCredentials);
      return NextResponse.json(
        {
          error: "à¸¢à¸±à¸‡à¹„à¸¡à¹ˆà¸žà¸£à¹‰à¸­à¸¡à¹ƒà¸Šà¹‰à¸‡à¸²à¸™ Facebook Ads API",
          message: "à¸‚à¸²à¸” Environment Variables à¸”à¸±à¸‡à¸™à¸µà¹‰:",
          missing: missingCredentials,
          instructions: {
            "ðŸ”§ à¸ªà¸³à¸«à¸£à¸±à¸š Local Development": {
              "1. à¸ªà¸£à¹‰à¸²à¸‡à¹„à¸Ÿà¸¥à¹Œ .env.local": "à¹ƒà¸™à¹‚à¸Ÿà¸¥à¹€à¸”à¸­à¸£à¹Œ package/",
              "2. à¹€à¸žà¸´à¹ˆà¸¡ credentials": "à¸”à¸¹à¸•à¸±à¸§à¸­à¸¢à¹ˆà¸²à¸‡à¹ƒà¸™ .env.local.example",
              "3. Restart dev server": "npm run dev",
            },
            "â˜ï¸ à¸ªà¸³à¸«à¸£à¸±à¸š Vercel (Production)": {
              "1. à¹„à¸›à¸—à¸µà¹ˆ":
                "https://vercel.com/your-project/settings/environment-variables",
              "2. à¹€à¸žà¸´à¹ˆà¸¡à¸•à¸±à¸§à¹à¸›à¸£à¹€à¸«à¸¥à¹ˆà¸²à¸™à¸µà¹‰": missingCredentials.join(", "),
              "3. Redeploy": "à¸à¸²à¸£ deploy à¸„à¸£à¸±à¹‰à¸‡à¸–à¸±à¸”à¹„à¸›à¸ˆà¸°à¹ƒà¸Šà¹‰à¸•à¸±à¸§à¹à¸›à¸£à¹ƒà¸«à¸¡à¹ˆ",
            },
            "ðŸ“š à¸§à¸´à¸˜à¸µà¸‚à¸­ Credentials": {
              "Facebook App": "https://developers.facebook.com/apps/",
              "Access Token":
                "https://developers.facebook.com/tools/accesstoken/",
              "Ad Account ID":
                "https://business.facebook.com/settings/ad-accounts",
              à¸„à¸¹à¹ˆà¸¡à¸·à¸­: "à¸”à¸¹à¸—à¸µà¹ˆ FACEBOOK_ADS_SETUP.md",
            },
          },
          currentCredentials: {
            hasAppId: !!credentials.appId,
            hasAppSecret: !!credentials.appSecret,
            hasAccessToken: !!credentials.accessToken,
            hasAdAccountId: !!credentials.adAccountId,
          },
          environment: process.env.VERCEL
            ? "Vercel Production"
            : "Local Development",
        },
        { status: 503 }
      );
    }
    console.log("âœ… All Facebook credentials available. Connecting to API...");
    try {
      // Dynamic import to avoid require()
      // @ts-expect-error - no types available for facebook-nodejs-business-sdk
      const bizSdk = await import("facebook-nodejs-business-sdk");
      const AdAccount = bizSdk.AdAccount;
      const Campaign = bizSdk.Campaign;
      // Initialize Facebook Ads API
      const api = bizSdk.FacebookAdsApi.init(credentials.accessToken);
      if (credentials.appSecret) {
        api.setDebug(false);
      }
      const account = new AdAccount(credentials.adAccountId);
      console.log("ðŸ” Fetching campaigns from Facebook Ads API...");
      // Fetch campaigns with insights
      const campaigns = await account.getCampaigns(
        [
          Campaign.Fields.id,
          Campaign.Fields.name,
          Campaign.Fields.status,
          Campaign.Fields.objective,
        ],
        {
          time_range: {
            since: startDate,
            until: endDate,
          },
        }
      );
      const campaignData: FacebookAdsCampaign[] = [];
      // Fetch insights for each campaign
      for (const campaign of campaigns) {
        try {
          const insights = await campaign.getInsights(
            ["impressions", "clicks", "spend", "cpm", "cpc", "ctr", "actions"],
            {
              time_range: {
                since: startDate,
                until: endDate,
              },
            }
          );
          if (insights && insights.length > 0) {
            const insight = insights[0];
            // à¸«à¸² conversions à¸ˆà¸²à¸ actions
            let conversions = 0;
            let costPerConversion = 0;
            if (insight.actions) {
              const conversionAction = insight.actions.find(
                (action: any) =>
                  action.action_type === "offsite_conversion.fb_pixel_purchase"
              );
              conversions = conversionAction
                ? parseFloat(conversionAction.value)
                : 0;
            }
            if (conversions > 0 && insight.spend) {
              costPerConversion = parseFloat(insight.spend) / conversions;
            }
            campaignData.push({
              id: campaign.id,
              name: campaign.name,
              status: campaign.status,
              objective: campaign.objective || "Unknown",
              impressions: parseInt(insight.impressions) || 0,
              clicks: parseInt(insight.clicks) || 0,
              spend: parseFloat(insight.spend) || 0,
              cpm: parseFloat(insight.cpm) || 0,
              cpc: parseFloat(insight.cpc) || 0,
              ctr: parseFloat(insight.ctr) || 0,
              conversions,
              costPerConversion,
            });
          }
        } catch (insightError) {
          console.error(
            `Error fetching insights for campaign ${campaign.id}:`,
            insightError
          );
        }
      }
      console.log(
        `âœ… Retrieved ${campaignData.length} campaigns with insights`
      );
      // à¸„à¸³à¸™à¸§à¸“ summary
      const summary = {
        totalImpressions: campaignData.reduce(
          (sum, c) => sum + c.impressions,
          0
        ),
        totalClicks: campaignData.reduce((sum, c) => sum + c.clicks, 0),
        totalSpend: campaignData.reduce((sum, c) => sum + c.spend, 0),
        averageCpm:
          campaignData.length > 0
            ? campaignData.reduce((sum, c) => sum + c.cpm, 0) /
              campaignData.length
            : 0,
        averageCpc:
          campaignData.length > 0
            ? campaignData.reduce((sum, c) => sum + c.cpc, 0) /
              campaignData.length
            : 0,
        averageCtr:
          campaignData.length > 0
            ? campaignData.reduce((sum, c) => sum + c.ctr, 0) /
              campaignData.length
            : 0,
        totalConversions: campaignData.reduce(
          (sum, c) => sum + c.conversions,
          0
        ),
      };
      const response: FacebookAdsResponse = {
        campaigns: campaignData,
        summary,
        dateRange: {
          startDate,
          endDate,
        },
      };
      return NextResponse.json(response);
    } catch (apiError: any) {
      console.error("âŒ Facebook Ads API Error:", apiError);
      let errorMessage = "à¹€à¸à¸´à¸”à¸‚à¹‰à¸­à¸œà¸´à¸”à¸žà¸¥à¸²à¸”à¹ƒà¸™à¸à¸²à¸£à¹€à¸Šà¸·à¹ˆà¸­à¸¡à¸•à¹ˆà¸­ Facebook Ads API";
      let errorDetails = apiError.message || "Unknown error";
      if (errorDetails.includes("access token")) {
        errorMessage = "Access Token à¹„à¸¡à¹ˆà¸–à¸¹à¸à¸•à¹‰à¸­à¸‡à¸«à¸£à¸·à¸­à¸«à¸¡à¸”à¸­à¸²à¸¢à¸¸";
        errorDetails =
          "à¸à¸£à¸¸à¸“à¸²à¸ªà¸£à¹‰à¸²à¸‡ Access Token à¹ƒà¸«à¸¡à¹ˆà¸—à¸µà¹ˆ https://developers.facebook.com/tools/accesstoken/";
      } else if (errorDetails.includes("permissions")) {
        errorMessage = "à¹„à¸¡à¹ˆà¸¡à¸µà¸ªà¸´à¸—à¸˜à¸´à¹Œà¹€à¸‚à¹‰à¸²à¸–à¸¶à¸‡";
        errorDetails =
          "à¸à¸£à¸¸à¸“à¸²à¸•à¸£à¸§à¸ˆà¸ªà¸­à¸šà¸§à¹ˆà¸² Access Token à¸¡à¸µ permissions: ads_read, ads_management";
      } else if (errorDetails.includes("account")) {
        errorMessage = "à¹„à¸¡à¹ˆà¸žà¸š Ad Account";
        errorDetails = `Ad Account ID ${credentials.adAccountId} à¹„à¸¡à¹ˆà¸–à¸¹à¸à¸•à¹‰à¸­à¸‡ à¸à¸£à¸¸à¸“à¸²à¸•à¸£à¸§à¸ˆà¸ªà¸­à¸šà¹ƒà¸™ Business Manager`;
      }
      return NextResponse.json(
        {
          error: errorMessage,
          details: errorDetails,
          credentials: {
            adAccountId: credentials.adAccountId,
            hasAccessToken: !!credentials.accessToken,
          },
        },
        { status: 500 }
      );
    }
  } catch (error) {
    console.error("âŒ Unexpected error:", error);
    return NextResponse.json(
      {
        error: "à¹€à¸à¸´à¸”à¸‚à¹‰à¸­à¸œà¸´à¸”à¸žà¸¥à¸²à¸”à¸—à¸µà¹ˆà¹„à¸¡à¹ˆà¸„à¸²à¸”à¸„à¸´à¸”",
        details: error instanceof Error ? error.message : "Unknown error",
      },
      { status: 500 }
    );
  }
}